import * as okayapi from '../../../components/okayapi/okayapi.js';

export default {
	data() {
		return {
			title: 'product-list',
			productList: [],
			input: "",
			renderImage: false
		};
	},
	methods: {
		loadData(action = 'add') {
			var params = {
				s: "App.Table.FreeQuery", // 必须，待请求的接口服务名称
				model_name: 'uniapp',
				where: '[["id", ">", "1"]]',
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					this.productList = this.productList.concat(res.data.data.list)
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});

		},
		searchOkay: function() {
			this.productList = []
			var params = {
				s: "App.Table.FreeQuery", // 必须，待请求的接口服务名称
				model_name: 'uniapp',
				where: '[["title", "LIKE", "' + this.input + '"]]',
			};
			uni.request({
				url: this.okayapiHost,
				method: 'GET',
				data: okayapi.enryptData(params),
				success: res => {
					console.log(res);
					this.productList = this.productList.concat(res.data.data.list)
				},
				fail: res => {
					console.log(res);
				},
				complete: () => {}
			});

		},
	},
	onLoad() {
		this.loadData();
		setTimeout(() => {
			this.renderImage = true;
		}, 300);
	},
	onPullDownRefresh() {
		this.loadData('refresh');
		// 实际开发中通常是网络请求，加载完数据后就停止。这里仅做演示，加延迟为了体现出效果。
		setTimeout(() => {
			uni.stopPullDownRefresh();
		}, 2000);
	},
	onReachBottom() {
		this.loadData();
	}
};
